

























































	import pageHead from '../../components/page-head.vue'

	export default {
		data: {
			title: 'accordion',
			lists: [{
					title: "图片轮播",
					type: 'swiper',
					show: true
				},
			],
			imgUrls: [
				'https://www.slluks.com/upload/image_collection/sllukst2.jpg',
				'https://www.slluks.com/upload/image_collection/1523583424.jpg',
				'https://www.slluks.com/upload/image_collection/1523583417.jpg'
			],
            latitude: 9.909,
            longitude: 116.39742,
            covers: [{
                latitude: 39.909,
                longitude: 116.39742,
                iconPath: '../../../static/images/menu1.png'
            }, {
                latitude: 39.90,
                longitude: 116.39,
                iconPath: '../../../static/images/menu1.png'
            }]
		},
		methods: {
			trigerCollapse(e) {
				for (let i = 0, len = this.lists.length; i < len; ++i) {
					if (e === i) {
						this.lists[i].show = !this.lists[i].show;
					} else {
						this.lists[i].show = false;
					}
				}
			}
		},
		components: {
			pageHead
		},
		onLoad: function (options) {
			uni.getStorage({
				key: 'storage_key',
				success: function (res) {
					console.log(res.data);
				}
			});
		},
		onPullDownRefresh() {
			uni.showToast({
				title: '刷新成功',
				duration: 2000,
				icon: 'none'
			});
			setTimeout(function () {
				uni.stopPullDownRefresh();
			}, 1000);
		}
	}
